import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import {FormControl, Validators, FormGroup} from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import { ActivatedRoute} from '@angular/router';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import { SmartSearchComponent } from '../../../layouts/smart-search/smart-search.component';
import { Router } from '@angular/router';

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./voucher-type-list.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [FLY_IN_OUT_ANIMATION,ROUND_ANTICLOCK_ANIMATION ],
})
export class VoucherTypeListComponent implements AfterViewInit {
	@ViewChild(SmartSearchComponent)smart: SmartSearchComponent;
	@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ListComponent) list: ListComponent;
	@ViewChild(ToastComponent) toast: ToastComponent;

	smartHide: boolean = true;
	animateSmart: string = 'out';
	vouchertypeId = 0;  
	msg = [];
	
	ngAfterViewInit(){
	}
	
    constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router) {
	}
	
   ngOnInit() {
		this.smart.smartUrl = sessionStorage.getItem('requestUrl')+"/axelacrm/inventory/vouchertype-smartsearch";
  		this.list.listUrl = sessionStorage.getItem('requestUrl')+"/axelacrm/inventory/group-list"; //path should be changed
	}   

	toggleAnimation(){
			this._router.navigate(['inventory/voucher-type-list/VoucherTypeadd'], {queryParams : { vouchertypeId : this.vouchertypeId}});
		 }
	 
	 smartClickListener(event){
		  this.list.listParam['requestType']='filter';
		  this.list.listParam['smartParam']=event;
		 this.list.getData();
	 }
	 
	  smartToggle(){
		  if(this.smartHide){
			  this.animateSmart = 'in';
			  this.smartHide = false;
		  }else{
			  this.animateSmart='out';
			  this.smartHide = true;
		  }
	  }
	 
	listClickListener(listParam: any[]){
		if(listParam[0] == 'updateVoucherType'){
			this.vouchertypeId = listParam[1];
			this.toggleAnimation();
//			this.voucherTypeForm.reset();
		}
	}
	 
}




