import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./voucher-type-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class VoucherTypeAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
//	@ViewChild(ToastComponent) toast: ToastComponent;
 
voucherTypeForm: FormGroup;
vouchertypeId = 0;  
ajaxData : VoucherTypeData[];
vouchertype= new VoucherTypeData();	
	
public vouchertypetypes = [];
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.vouchertypeId).subscribe(params=>{
			if(params.vouchertypeId != undefined){
				this.vouchertypeId=params.vouchertypeId; 
			}
		}); 
	console.log("vouchertypeId=="+this.vouchertypeId);
	if(this.vouchertypeId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.voucherTypeForm = new FormGroup({			
			'vouchertypeName' : new FormControl('', [ Validators.required]),
			 'vouchertypeLabel' : new FormControl(''),
			 'vouchertypeBaseType' : new FormControl(''),
			 'vouchertypeNumbering' : new FormControl(''),
			 'vouchertypePrefix' : new FormControl(''),
			 'vouchertypeSuffix' : new FormControl(''),
			 'vouchertypeBankCashCustomerRestriction' : new FormControl(''), 
			 'vouchertypeMobile' : new FormControl(''),
			 'vouchertypeEmail' : new FormControl(''),
			 'vouchertypeDOB' : new FormControl(''),
			 'vouchertypeDND' : new FormControl(''),
			 'vouchertypeBillingAdd' : new FormControl(''),
			 'vouchertypeConsigneeAdd' : new FormControl(''),
			 'vouchertypeTerms' : new FormControl(''),
			 'vouchertypeRefNoEnable' : new FormControl(''),
			 'vouchertypeRefNoMandatory' : new FormControl(''),
			 'vouchertypeEmailEnable' : new FormControl(''),
			 'vouchertypeEmailAuto' : new FormControl(''),
			 'vouchertypeEmailSub' : new FormControl(''),
			 'vouchertypeEmailFormat' : new FormControl(''),
			 'vouchertypeSMSEnable' : new FormControl(''),
			 'vouchertypeSMSAuto' : new FormControl(''),
			 'vouchertypeSMSFormat' : new FormControl(''),
			 'vouchertypeActive' : new FormControl(''),
			 'vouchertypeRank' : new FormControl(''),
			 'vouchertypeEntryId' : new FormControl(''),
			 'vouchertypeEntryDate' : new FormControl(''),
			 'vouchertypeModifiedId' : new FormControl(''),
			 'vouchertypeModifiedDate' : new FormControl(''),
		});
	}
	  
	   	get vouchertypeName(){ return this.voucherTypeForm.get('vouchertypeName')};
		 get vouchertypeLabel(){ return this.voucherTypeForm.get('vouchertypeLabel')};
		 get vouchertypeBaseType(){ return this.voucherTypeForm.get('vouchertypeBaseType')};
		 get vouchertypeNumbering(){ return this.voucherTypeForm.get('vouchertypeNumbering')};
		 get vouchertypePrefix(){ return this.voucherTypeForm.get('vouchertypePrefix')};
		 get vouchertypeSuffix(){ return this.voucherTypeForm.get('vouchertypeSuffix')};
		 get vouchertypeBankCashCustomerRestriction(){ return this.voucherTypeForm.get('vouchertypeBankCashCustomerRestriction')}; 
		 get vouchertypeMobile(){ return this.voucherTypeForm.get('vouchertypeMobile')};
		 get vouchertypeEmail(){ return this.voucherTypeForm.get('vouchertypeEmail')};
		 get vouchertypeDOB(){ return this.voucherTypeForm.get('vouchertypeDOB')};
		 get vouchertypeDND(){ return this.voucherTypeForm.get('vouchertypeDND')};
		 get vouchertypeBillingAdd(){ return this.voucherTypeForm.get('vouchertypeBillingAdd')};
		 get vouchertypeConsigneeAdd(){ return this.voucherTypeForm.get('vouchertypeConsigneeAdd')};
		 get vouchertypeTerms(){ return this.voucherTypeForm.get('vouchertypeTerms')};
		 get vouchertypeRefNoEnable(){ return this.voucherTypeForm.get('vouchertypeRefNoEnable')};
		 get vouchertypeRefNoMandatory(){ return this.voucherTypeForm.get('vouchertypeRefNoMandatory')};
		 get vouchertypeEmailEnable(){ return this.voucherTypeForm.get('vouchertypeEmailEnable')};
		 get vouchertypeEmailAuto(){ return this.voucherTypeForm.get('vouchertypeEmailAuto')};
		 get vouchertypeEmailSub(){ return this.voucherTypeForm.get('vouchertypeEmailSub')};
		 get vouchertypeEmailFormat(){ return this.voucherTypeForm.get('vouchertypeEmailFormat')};
		 get vouchertypeSMSEnable(){ return this.voucherTypeForm.get('vouchertypeSMSEnable')};
		 get vouchertypeSMSAuto(){ return this.voucherTypeForm.get('vouchertypeSMSAuto')};
		 get vouchertypeSMSFormat(){ return this.voucherTypeForm.get('vouchertypeSMSFormat')};
		 get vouchertypeActive(){ return this.voucherTypeForm.get('vouchertypeActive')};
		 get vouchertypeRank(){ return this.voucherTypeForm.get('vouchertypeRank')};
		get vouchertypeEntryId(){ return this.voucherTypeForm.get('vouchertypeEntryId')};
		get vouchertypeEntryDate(){ return this.voucherTypeForm.get('vouchertypeEntryDate')};
		get vouchertypeModifiedId(){ return this.voucherTypeForm.get('vouchertypeModifiedId')};
		get vouchertypeModifiedDate(){ return this.voucherTypeForm.get('vouchertypeModifiedDate')};

	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<VoucherTypeData>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer/customer-data ',
							 			JSON.stringify({ 'vouchertypeId' :this.vouchertypeId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.vouchertype = data['populatadata'][0];
																		console.log("vouchertypeModifiedId=="+this.vouchertype.vouchertypeModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/customer-group-list']);      //path should be changed
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.vouchertype[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.voucherTypeForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.vouchertypeId == 0){ 
				this.vouchertype['requestType'] = 'add';
			}else{
				this.vouchertype['requestType'] = 'update';
				this.vouchertype['vouchertypeId'] = this.vouchertypeId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/vouchertype',
									JSON.stringify(this.vouchertype),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
	//			  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.voucherTypeForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.vouchertype);
	  }
 
  onDelete(){
		 this.vouchertype['requestType'] = 'delete';
		this.vouchertype['vouchertypeId'] = this.vouchertypeId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/inventory/vouchertype',
									JSON.stringify(this.vouchertype),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
//			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.voucherTypeForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}
	
export class VoucherTypeData {
    vouchertypeId: string;
 vouchertypeName: string;
 vouchertypeLabel: string;
 vouchertypeBaseType: string;
 vouchertypeNumbering: string;
 vouchertypePrefix: string;
 vouchertypeSuffix: string;
 vouchertypeBankCashCustomerRestriction: string; 
 vouchertypeMobile: string;
 vouchertypeEmail: string;
 vouchertypeDOB: string;
 vouchertypeDND: string;
 vouchertypeBillingAdd: string;
 vouchertypeConsigneeAdd: string;
 vouchertypeTerms: string;
 vouchertypeRefNoEnable: string;
 vouchertypeRefNoMandatory: string;
 vouchertypeEmailEnable: string;
 vouchertypeEmailAuto: string;
 vouchertypeEmailSub: string;
 vouchertypeEmailFormat: string;
 vouchertypeSMSEnable: string;
 vouchertypeSMSAuto: string;
 vouchertypeSMSFormat: string;
 vouchertypeActive: string;
 vouchertypeRank: string;
 vouchertypeEntryId: string;
 vouchertypeEntryDate: string;
 vouchertypeModifiedId: string;
 vouchertypeModifiedDate: string;
}


